<template>
  <div>
    <header class="per_center">
      <div class="pre_title">
        <router-link class="icon-Artboard16" to="/index"></router-link>
      </div>
      <div class="pre_head">
        <div class="pre_head_l">
          <p>欢迎您</p>
          <h3 class="pl0 mt0">{{baseMsg.wxName?baseMsg.wxName:'游客用户'}}</h3>
        </div>
        <div class="pre_head_r">
          <div class="r_img">
            <img v-if="baseMsg.wxHead" :src="baseMsg.wxHead" alt="">
            <img v-else src="../assets/Art7.png" alt="">
          </div>
        </div>
      </div>
    </header>
    <section class="">
      <div class="login_body">
        <div class="preTitle">
          <p class="fnt2 mb25">验证手机号</p>
          <p class="fnt3 mb25"><i class="colorYellow">您还没有验证手机号</i>,无法查看订单</p>
        </div>
        <!--手机号码-->
        <inputText class="iptStyle" v-model="req_login.mobile" type="tel" title="手机号"  @validate="ismobile=true"></inputText>
        <!--验证码-->
        <div class="yz-code">
          <inputText class="iptStyle" type="code" v-model="req_login.verifyCode" title="验证码"  @validate="isverifyCode=true" :showCancel="false"></inputText>
          <div class="detBtn YCode" :class="[{'nochose':!ismobile},{'noclick':!iScode}]" @click="getCode">
            <p >{{codeText}}</p>
          </div>
        </div>
      </div>
    </section>
    <p v-if="ismobile&&isverifyCode" class="login_box_p mt_35 mt7">
      <a href="javaScript:void(0)" @click="login" class="login_box_p_a01">确定</a>
    <p v-else class="login_box_p mt_35 op4 mt7"><a href="javaScript:void(0)" class="login_box_p_a01">确定</a>
    </p>
  </div>
</template>

<style scoped>

</style>
<script>
  export default {
    data () {
      return {
        codeText: '获取验证码',
        iScode: true,
        islogin: true,
        isvalidate: true,
        ismobile: false,
        isverifyCode: false,
        baseMsg: '',
        req_login:{
          mobile:'',
          verifyCode:'',
        }
      }
    },
    filters: {

    },
    watch: {
//      ismobile(val){
//        console.log(val,'验证信息');
//      },
    },
    created: function () {
      this.baseInfo()
    },
    mounted: function () {

    },

    methods: {
      getCode(){
        var _t = this;
        if(this.ismobile&&_t.iScode){
          this.sendCode()
        }
      },

      baseInfo(){
        var _t = this;
        this.$ajax.post('/common/queryUserInfo.shtml').then(function (res) {
          if (res.data.result == 1) {
            _t.baseMsg = res.data.model;
            var title = res.data.model.hotelName
            document.title = title
          }
        }).catch(function (err) {

        });
      },

      login(){
        var _t = this;
        var data = this.req_login;
        this.$ajax.post('/common/b2c/mobileLogin.shtml', data).then(function (res) {
          if (res.data.result == 1) {
            _t.$router.push('/personal');
          }
        }).catch(function (err) {

        });
      },

      sendCode(){
        var _t = this;
        this.$ajax.get('/common/sendVerifyCode.shtml?mobile='+this.req_login.mobile).then(function (res) {
          if (res.data.result == 1) {
            console.log('begin',_t.iScode,'this.iScode');
//            if (_t.iScode) {
              console.log('ing');
              _t.iScode = false;
              _t.codeText = '60S'
              var num = 60;
              var time = setInterval(function () {
                num--;
                if (num < 0) {
                  _t.iScode = true;
                  _t.codeText = '重新获取'
                  clearInterval(time)
                } else {
                  _t.codeText = num + 'S';
                }
              }, 1000)
//            }
          }
        }).catch(function (err) {

        });
      },
    }
  }
</script>



